<template>
    <div>
      <mt-header fixed title="资讯"></mt-header>
      <mt-loadmore :top-method="loadTop" ref="loadmore">
        <InfoList class="infolist" :dataList="dataList" v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10"></InfoList>
      </mt-loadmore>
      <mt-tabbar fixed v-model="tabbarSelected">
        <mt-tab-item class="tabbar-item-info" id="资讯">
          <span class="iconfont item-font" slot="icon">&#xe75e;</span>
          资讯
        </mt-tab-item>
        <mt-tab-item class="tabbar-item-icon" id="行情">
          <span class="iconfont item-font" slot="icon">&#xe757;</span>
          行情
        </mt-tab-item>
      </mt-tabbar>
    </div>
</template>

<script>
import InfoList from './InfoList'

export default {
  name: 'home',
  components: {
    InfoList
  },
  data () {
    return {
      dataList: [],
      tabbarSelected: '',
      loading: false,
      page: 1
    }
  },
  watch: {
    tabbarSelected (val) {
      if (val === '行情') {
        this.$router.push('/market')
      }
    }
  },
  methods: {
    getHomeInfo () {
      this.$store.dispatch('infoListData', {
        page: this.page
      }).then(res => {
        if (res) {
          if (res.page === this.page) {
            this.loading = true
          } else {
            this.loading = false
          }
          if (this.page === 1) {
            this.dataList = res.list
          } else {
            this.dataList = this.dataList.concat(res.list)
          }
          this.$refs.loadmore.onTopLoaded()
        }
      })
    },
    loadTop () {
      this.page = 1
      this.getHomeInfo()
    },
    loadMore () {
      this.page++
      this.getHomeInfo()
    }
  },
  mounted () {
    this.loading = true
    this.getHomeInfo()
  }
}
</script>

<style lang="stylus" scoped>
  .infolist
    display: flex
    flex-direction: column
    margin-top: 1rem
    margin-bottom: 1rem
  .item-font
    font-size .45rem
  .tabbar-item-info
    color #1ea9ea
  .tabbar-item-icon
    color gray
</style>



